"use client";

import { useState } from "react";

export default function YearSelector() {
  const [selectedYear, setSelectedYear] = useState(null);

  const handleYearClick = (year) => {
    setSelectedYear(year);
  };

  const handleNext = () => {
    if (selectedYear) {
      console.log("Selected year:", selectedYear);
      // Placeholder for navigation to next page
    }
  };

  // Generate years from 1900 to current year (2025)
  const currentYear = new Date().getFullYear();
  const years = Array.from({ length: currentYear - 1899 }, (_, i) => currentYear - i);

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gradient-to-b from-gray-900 to-black text-white font-sans">
      <h1 className="text-5xl font-bold mb-8 text-yellow-400 tracking-wide animate-pulse">Rec&apos;One</h1>
      <p className="text-lg mb-6 text-gray-300 italic">Discover your perfect movie, one recommendation at a time</p>
      <div className="text-center">
        <h2 className="text-xl font-semibold text-gray-200 mb-4">Choose a Year</h2>
        <div className="grid grid-cols-5 gap-2 max-w-2xl mx-auto p-4 max-h-96 overflow-y-auto">
          {years.map((year) => (
            <button
              key={year}
              onClick={() => handleYearClick(year)}
              className={`p-2 rounded-lg transition duration-200 ${
                selectedYear === year
                  ? "bg-yellow-400 text-black"
                  : "bg-gray-800 text-gray-200 hover:bg-yellow-400 hover:text-black"
              }`}
            >
              {year}
            </button>
          ))}
        </div>
        <button
          onClick={handleNext}
          disabled={!selectedYear}
          className="mt-6 px-6 py-2 bg-yellow-400 text-black font-semibold rounded-lg hover:bg-yellow-500 transition duration-300 transform hover:scale-105 disabled:opacity-50 disabled:cursor-not-allowed"
        >
          Next
        </button>
      </div>
      <div className="absolute bottom-4 text-gray-500 text-sm">Powered by subreddit sentiment analysis</div>
    </div>
  );
}